.header {
  margin: 0 auto;
}
.header-left {
  float: left;
  padding: 0 15px;
  line-height: 20px;
  list-style-type: none;
}

.header-right {
  float: right;
}
.header-nav {
  float: left;
  padding: 0 10px;
  border-left: 1px solid #e6e9ed;
  border-right: 1px solid #e6e9ed;
  line-height: 38px;
  vertical-align: middle;
}
